import styled from 'styled-components'
import RestartAltIcon from '@mui/icons-material/RestartAlt'
import PlayArrowIcon from '@mui/icons-material/PlayArrow'


const HeaderDiv = styled.div`
  height: 80px;
  width: 100%;
  background: #000;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  z-index: 1;

  h1 {
    margin-right: 16px;
    font-family: 'Architects Daughter', cursive;
    font-size: 40px;
    cursor: pointer;
  }

  @media screen and (max-width: 480px) {
    h1 {
      font-size: 27px;
    }
  }
`;

const OuterIconContainer = styled.div`
  position: relative;
  width: 0;
  display: flex;
`

const Icon = styled.div`
  width: 30px;
  height: 30px;
  border: 2px solid #d8bfff;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;

  &.disabled {
    background-color: #444444;
    cursor: not-allowed;
  }
`


function Header(params) {
  const {
    seedSong,
    seedArtist,
    seedGenre,
    fetchRecommendations
  } = params

  const isDisabled = () => {
    return !seedSong && !seedArtist && !seedGenre
  }

  const goHome = () => {
    document.location.href="/"
  }

  return (
    <HeaderDiv className="suggestr-header">
      <h1 onClick={goHome}>Suggestr</h1>
      <OuterIconContainer>
        <div>
          <Icon onClick={goHome}>
            <RestartAltIcon/>
          </Icon>
        </div>
        <div>
          <Icon onClick={fetchRecommendations} className={isDisabled() ? 'disabled' : ''}>
            <PlayArrowIcon/>
          </Icon>
        </div>
      </OuterIconContainer>
    </HeaderDiv>
  );
}

export default Header;
